import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Avatar from '@material-ui/core/Avatar';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';

import TextField from '@material-ui/core/TextField';
import Carousel from '../slider/slider'
import Button from '@material-ui/core/Button'
import { withRouter, Link } from 'react-router-dom';
import SideBar from '../sidebar/sidebar'
import Cookies from "js-cookie"
import { connect, useSelector } from 'react-redux'
import { getMyAd, getAllAds } from "../../Redux/Actions/adsAction";
import baseUrl from '../../Redux/config';

const images = [
  {
    // url: '/static/images/grid-list/breakfast.jpg',
    url: 'https://wallup.net/wp-content/uploads/2016/01/138584-ladybugs-nature-plants.jpg',


    title: 'Add New Post',
    width: '10%',
    height: '5%'
  },
]
const useStyles = makeStyles((theme) => ({


  //.................................................................................


  blogsContainer: {
    paddingTop: theme.spacing(3),
    marginTop: 80
  },
  blogTitle: {
    fontWeight: 800,
    paddingBottom: theme.spacing(3)
  },
  card: {
    maxWidth: "100%",
  },
  media: {
    height: 240
  },
  cardActions: {
    display: "flex",
    margin: "0 10px",
    justifyContent: "space-between"
  },
  author: {
    display: "flex"
  },
  paginationContainer: {
    display: "flex",
    justifyContent: "center"
  },

  adjSearch: {
    width: '500px',
    fontSize: '30px',
    paddingTop: 0,
    marginLeft: '35%',
    marginBottom: 20,
    paddingBottom: 5


  },
  Btn1: {
    fontSize: '16px',
    float: "rgiht",
    marginLeft: '25px',
    marginBottom: 20
  }


}));

function MyAccount(props) {
  let { ads, getMyAd, getAllAds } = props
  const classes = useStyles()
  const user = useSelector(state => state.auth.user)
  const [allAds, setAds] = useState([])

  useEffect(() => {
    if (user.type == "nursery") {
      getMyAd({ user_id: user._id })
    }
    if (user.type == "user") {
      getAllAds()
    }
  }, [])
  useEffect(() => {
    setAds(ads.ads)
  }, [ads.ads])


  console.log(allAds);


  return (
    <>
      <div className="display">

        <div className="box1">
          <SideBar />
        </div>

        <div className="box2">

        </div>
      </div>
      <div>


        <Container maxWidth="lg" className={classes.blogsContainer}>
          <Typography variant="h4" className={classes.blogTitle}>
            My Account <i class="fa fa-chevron-right" aria-hidden="true"></i>  Welcome Mr. {user ? user.fullname : null}
          </Typography>
          <TextField id="filled-search" label="" className={classes.adjSearch} type="search" variant="filled" />

          <Button className={classes.Btn1} variant="contained" color="primary">
            Search
          </Button>
          {user
            ?
            user.type == "nursery" ?
              <Link to='/newpost'>

                <Button className={classes.Btn1} variant="contained" color="primary">
                  Add New Post
                </Button>

              </Link>
              : null : null
          }

          {/* //................................................................................... */}

          <Grid container spacing={3}>
            {allAds ? allAds.length > 0 ? allAds.map((item, key) => {
              return <Grid item xs={12} sm={6} md={4} key={key} >
                <Card className={classes.card}>
                  <CardActionArea>
                    <CardMedia
                  className="animate__animated animate__flipInX"
                  style={{ height: 240 }}
                      image={baseUrl + `/uploads/${item.images[0]}`}
                      title="Contemplative Reptile"
                    />
                    <CardContent>
                      <Typography gutterBottom variant="h4" component="h2">
                        {item.plant_title}
                      </Typography>
                      <Typography variant="h6" color="textSecondary" component="p">
                        {item.plant_description}
                      </Typography>
                    </CardContent>
                  </CardActionArea>
                  <CardActions className={classes.cardActions}>
                    <Box className={classes.author}>
                      <Avatar src={`/uploads/${item.images[2]}`} />
                      <Box ml={2}>
                        <Typography variant="h5" component="p">
                          {item.nursery_name}
                        </Typography>
                        <Typography variant="h6" color="textSecondary" component="p">
                          {item.created_at.slice(0, 10)}
                        </Typography>
                      </Box>
                    </Box>
                    <Box>
                      RS. {item.price_plant}
                    </Box>
                  </CardActions>
                </Card>
              </Grid>
            }) : null : null}



          </Grid>


        </Container>

      </div>
    </>
  )
}

let mapStateToProps = store => {
  return {
    ads: store.ads
  }
}

export default withRouter(connect(mapStateToProps, { getMyAd, getAllAds })(MyAccount))