import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Avatar from '@material-ui/core/Avatar';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';

import Carousel from '../slider/slider'
import { withRouter, Link } from 'react-router-dom';
import SideBar from '../sidebar/sidebar'
import { connect, useSelector } from 'react-redux'
import { getMyAd, getAllAds } from "../../Redux/Actions/adsAction";
import baseUrl from '../../Redux/config';

import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import DirectionsIcon from '@material-ui/icons/Directions';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const useStyles = makeStyles((theme) => ({

  blogsContainer: {
    paddingTop: theme.spacing(3),
    marginTop: 80,
    width: "94%",
    marginLeft: "6%"
  },
  blogTitle: {
    fontWeight: 800,
    paddingBottom: theme.spacing(3),
    marginLeft: 20,
    marginTop: 55
  },
  card: {
    maxWidth: "100%",
  },
  media: {
    height: 240
  },
  cardActions: {
    display: "flex",
    margin: "0 10px",
    justifyContent: "space-between"
  },
  author: {
    display: "flex"
  },
  paginationContainer: {
    display: "flex",
    justifyContent: "center"
  },
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: 400,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
    fontSize: 14
  },
  iconButton: {
    padding: 10,
    "&:hover": {
      color: "black"
    }
  },
  divider: {
    height: 28,
    margin: 4,
  },
  flex: {
    display: 'flex',
    justifyContent: 'flex-end'
  },
  formControl: {
    marginRight: 20,
    minWidth: 150,

  },
}));



function Product(props) {
  let { ads, getMyAd, getAllAds } = props
  const classes = useStyles()
  const [allAds, setAds] = useState([])

  useEffect(() => {
    getAllAds()
  }, [])
  useEffect(() => {
    setAds(ads.ads)
  }, [ads.ads])
  const [age, setAge] = React.useState('');

  const handleChange = (event) => {
    setAge(event.target.value);
  };

  return (

    <>
      <div className="display">

        <div className="box1">
          <SideBar />
        </div>

        <div className="box2">

        </div>
      </div>

      <div>

        <Container maxWidth="lg" className={classes.blogsContainer}>
          <div className={classes.flex}>

            <FormControl variant="outlined" className={classes.formControl}>
              <InputLabel id="demo-simple-select-outlined-label">
                Select Category
              </InputLabel>
              <Select
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                value={age}
                onChange={handleChange}
                label="Category"
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                <MenuItem value="Herbs">Herbs</MenuItem>
                <MenuItem value="Shrubs">Shrubs</MenuItem>
                <MenuItem value="Trees">Trees</MenuItem>
                <MenuItem value="Climbers">Climbers</MenuItem>
                <MenuItem value="Creepers">Creepers</MenuItem>
              </Select>
            </FormControl>
            <Paper component="form" className={classes.root}>
              <InputBase
                className={classes.input}
                placeholder="Search Plants"
                inputProps={{ 'aria-label': 'search google maps' }}
              />
              <IconButton type="submit" className={classes.iconButton} aria-label="search">
                <SearchIcon />
              </IconButton>

            </Paper>
          </div>
          <Typography variant="h4" className={classes.blogTitle}>
            Products
        </Typography>
          <Grid
            container spacing={3}
          >

            {allAds ? allAds.length > 0 ? allAds.map((item, key) => {
              return <Grid item xs={12} sm={6} md={4} key={key}>
                <Card className={classes.card}>
                  <CardActionArea>
                    <Link to="/postDetail">
                      <CardMedia
                        className="animate__animated animate__flipInX"
                        style={{ height: 240 }}
                        image={baseUrl + `/uploads/${item.images[0]}`}
                        title="Contemplative Reptile"
                      />
                    </Link>
                    <CardContent>
                      <Typography gutterBottom variant="h4" component="h2">
                        {item.plant_title}
                      </Typography>
                      <Typography variant="h6" color="textSecondary" component="p">
                        {item.plant_description}
                      </Typography>
                    </CardContent>
                  </CardActionArea>
                  <CardActions className={classes.cardActions}>
                    <Box className={classes.author}>
                      <Avatar src={`/uploads/${item.images[2]}`} />
                      <Box ml={2}>
                        <Typography variant="h5" component="p">
                          {item.nursery_name}
                        </Typography>
                        <Typography variant="h6" color="textSecondary" component="p">
                          {item.created_at.slice(0, 10)}
                        </Typography>
                      </Box>
                    </Box>
                    <Box>
                      RS. {item.price_plant}
                    </Box>
                  </CardActions>
                </Card>
              </Grid>
            }) : null : null}

          </Grid>
        </Container>

      </div>

    </>)
}

let mapStateToProps = store => {
  return {
    ads: store.ads
  }
}

export default withRouter(connect(mapStateToProps, { getMyAd, getAllAds })(Product))